<template>
	<view style="padding: 40rpx;">
		<view style="padding: 20rpx; margin: 80rpx 0; background-color: #fff; box-shadow: 0 2rpx 10rpx rgba(0,0,0,.1); border-radius: 10rpx;">
			<view style="margin: 50rpx 30rpx; font-size: 40rpx;">欢迎登录</view>
			<uni-forms ref="form" :modelValue="form" :rules="rules" validateTrigger='blur'>
				<uni-forms-item v-if="!seen" name="telephone" required>
					<uni-easyinput prefixIcon="phone" v-model="form.telephone" placeholder="请输入手机号" />
				</uni-forms-item>
				<uni-forms-item v-else name="username" required>
					<uni-easyinput prefixIcon="person" v-model="form.username" placeholder="请输入账号" />
				</uni-forms-item>
				<uni-forms-item name="password" required>
					<uni-easyinput type="password" prefixIcon="locked" v-model="form.password" placeholder="请输入密码" />
				</uni-forms-item>
				<uni-forms-item>
					<button @click="login()" style="background-color: #ffd100; border-color: #ffd100; height: 70rpx; line-height: 70rpx;">登 录</button>
				</uni-forms-item>
				<uni-forms-item>
					<view style="text-align: right;">还没有账号？去 <navigator style="display: inline-block; color: dodgerblue; 
						margin-left: 4rpx;" url="/pages/register/register">注册</navigator></view>
				</uni-forms-item>
				<uni-forms-item>
					<button  class="switch-login-btn" @click="onchange()"  hover-class="none">{{ seen ? '使用手机号登录' : '使用账号登录' }}</button>
				</uni-forms-item>
			</uni-forms>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					role: 'USER'
				},
				rules: {
					// 对username字段进行必填验证
					username: {
						// username 字段的校验规则
						rules:[
							// 校验 username 不能为空
							{
								required: true,
								errorMessage: '请输入账号',
							},
							// 对username字段进行长度验证
							{
								minLength: 3,
								maxLength: 10,
								errorMessage: '账号长度在 {minLength} 到 {maxLength} 个字符',
							}
						],
					},
					password: {
						rules:[
							{
								required: true,
								errorMessage: '请输入密码',
							},
							{
								minLength: 3,
								maxLength: 10,
								errorMessage: '密码长度在 {minLength} 到 {maxLength} 个字符',
							}
						],
					},
					telephone: {
						rules:[
							{
								required: true,
								errorMessage: '请输入手机号',
							},
							{
								minLength: 11,
								maxLength: 11,
								errorMessage: '手机号应为 {minLength}个字符',
							}
						],
					}
					
				},
				seen:true
			}
		},
		methods: {
			login() {
				this.$refs.form.validate().then(res=>{
					this.$request.post('/login', this.form).then(res => {
						if (res.code === '200') {
							uni.showToast({
								icon: 'success',
								title: '登录成功'
							})
							uni.setStorageSync('xm-user', res.data)
							
							// 跳转主页
							uni.switchTab({
								url: '/pages/index/index'
							})
						} else {
							uni.showToast({
								icon: 'error',
								title: res.msg
							})
						}
					})
				}).catch(err =>{
					console.log('表单错误信息：', err);
				})
			},
			onchange() {
							this.seen = !this.seen; // 切换状态
							// 清空切换后的输入框
							if (this.seen) {
								this.form.telephone = '';
							} else {
								this.form.username = '';
							}
							// 清除表单验证状态
							this.$refs.form.clearValidate();
						}
		}
	}
</script>

<style>
/* 外联样式 */
.switch-login-btn {
  /* 尺寸控制 */
  height: 60rpx !important;
  line-height: 60rpx !important;
  min-width: 240rpx !important;
  
  /* 背景与边框 */
  background-color: #fff !important;
  border: none !important;
  
  /* 文字样式 */
  color: #666 !important;
  font-size: 26rpx !important;
  
  /* 布局定位 */
  display: block;
  margin: 20rpx auto 0;
  padding: 0 30rpx;
  
  /* 去除默认效果 */
  border-radius: 0;
  box-shadow: none;
}

/* 可选：添加点击态 */
.switch-login-btn:active {
  opacity: 0.8;
}
</style>
